.big-icon {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: $global-padding 2em;
  color: $color-base;

  @include breakpoint(small) {
    &:nth-child(odd) {
      background-color: $color-gray-lightest;
    }
  }

  @include breakpoint(medium) {
    &:nth-child(odd) {
      border-right: $global-border-width solid $global-border-color;
    }

    &:nth-child(4n-3),
    &:nth-child(4n-2) {
      background-color: $color-gray-lightest;
    }

    &:nth-child(4n-1),
    &:nth-child(4n) {
      background-color: transparent;
    }

    &:nth-child(4n-3) {
      &:before {
        position: absolute;
        top: 0;
        bottom: 0;
        left: -4.5em;
        z-index: -1;
        width: 4.5em;
        background-color: $color-gray-lightest;
        content: '';
      }
    }
  }

  @include breakpoint(large) {
    &:nth-child(3n-2) {
      border-right: $global-border-width solid $global-border-color;
    }

    &:nth-child(3n-1) {
      border-right: 0;
    }

    &:nth-child(3n) {
      border-right: 0;
      border-left: $global-border-width solid $global-border-color;
    }

    &:nth-child(6n-5),
    &:nth-child(6n-4),
    &:nth-child(6n-3) {
      background-color: $color-gray-lightest;
    }

    &:nth-child(6n-2),
    &:nth-child(6n-1),
    &:nth-child(6n) {
      background-color: transparent;
    }

    &:nth-child(4n-3) {
      &:before {
        background-color: transparent;
      }
    }

    &:nth-child(6n-5) {
      &:before {
        position: absolute;
        top: 0;
        bottom: 0;
        left: -4.5em;
        z-index: -1;
        width: 4.5em;
        background-color: $color-gray-lightest;
        content: '';
      }
    }
  }

  &:active,
  &:focus,
  &:hover {
    h3 {
      color: $color-primary;
    }

    .path-dark-gray {
      fill: $color-primary;
    }
  }

  svg {
    display: block;
    max-width: 65%;
    max-height: 10em;
    margin: 2em auto;
  }

  h3 {
    margin: 0;
    font-size: 1.75rem;
    color: $color-gray-dark;
    text-align: center;
    transition: all 0.5s ease;
  }
}

.header-big-icons.row {
  margin: 0 -1rem;

  > button {
    display: flex;
    align-items: stretch;
    flex-direction: column;
    justify-content: space-between;
    padding: 2em;

    &:first-child {
      @include breakpoint(large) {
        border-right: $global-border-width solid $color-gray;
      }
    }

    &:last-child {
      @include breakpoint(large) {
        border-left: $global-border-width solid $color-gray;
      }
    }

    &:active,
    &:focus,
    &:hover {
      .path-primary {
        fill: $color-primary-alt-light;
      }
    }
  }

  h3 {
    position: relative;
    z-index: 1;
    margin: 0;
    text-align: center;
  }

  svg {
    position: relative;
    z-index: 0;
    display: block;
    max-width: 100%;
    max-height: 11em;
    margin: 0 auto 1em;
  }

  p {
    text-align: left;

    &:last-child {
      margin: 0;
    }
  }

  #protect-yourself-anim svg {
    max-height: 10em;
    margin-top: 1em;
  }
}

.row-header {
  .button-report {
    width: 100%;
    border: 0;

    @include breakpoint(large) {
      font-size: 1.5em;
    }

    .path-primary {
      fill: $color-secondary-darkest;
    }
  }

  .button-report-adjacent {
    border-left: $global-border-width solid $color-gray;
  }

  .button-report-container {
    margin: 0 -1rem;
  }
}

.row .home-content.row {
  margin: -1em;

  @include breakpoint(medium) {
    margin: -2em;
  }
}
