body {
  height: 100%;
  min-height: 100vh;
  overflow-y: visible;
}

.off-canvas-wrapper {
  overflow: hidden;
  background-color: $offcanvas-left-background;
}

.off-canvas-content {
  min-height: calc(100vh - 28px);
  overflow-y: visible;
  background-color: $offcanvas-content-background;
  outline: 0;

  > .row {
    margin-right: auto;
    margin-left: 0;
  }
}

.js-off-canvas-exit {
  width: 0;
  height: 0;
}

.off-canvas {
  position: absolute;
  z-index: -1;
  overflow-y: auto;
  font-size: 0.9em;
  color: $offcanvas-left-color;
  background-color: $offcanvas-left-background;
  border-color: $offcanvas-left-border-color;
  border-style: solid;
  border-width: $offcanvas-top-border-size $offcanvas-left-border-size 0 0;
  outline: 0;

  &.position-right {
    color: $offcanvas-right-color;
    background-color: $offcanvas-right-background;
    border-color: $offcanvas-right-border-color;
    border-width: $offcanvas-top-border-size 0 0 $offcanvas-right-border-size;

    a {
      color: $offcanvas-right-link;

      &:active,
      &:focus,
      &:hover {
        color: $offcanvas-right-link-active;
      }
    }
  }

  a {
    color: $offcanvas-left-link;

    &:active,
    &:focus,
    &:hover {
      color: $offcanvas-left-link-active;
    }
  }

  h2 {
    font-size: 1.5em;
  }

  h3 {
    font-size: 1.2em;
  }
}
