.row-footer {
  font-size: 0.8em;
  color: $white;
  text-align: center;
  background-color: $color-gray-dark;

  > .columns > * {
    margin-right: auto;
    margin-left: auto;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    color: $color-primary-alt-light;
    border-bottom: $global-border-width solid $color-primary-alt-light;

    &:active,
    &:focus,
    &:hover {
      color: $white;
      border-bottom-color: $white;
    }
  }

  ul {
    max-width: 40em;
    list-style: none;
  }

  .column-border:first-child {
    border-right: $global-border-width solid $color-gray;
  }

}
