.columns.content-sidebar {
  padding: 0 1rem 1rem;
  font-size: 0.75em;

  @include breakpoint(medium) {
    padding: 2.5rem 0 1rem 1rem;
  }

  &:empty {
    display: none;
  }

  a {
    width: 100%;
    color: $color-base;
    border-top: $global-border-width solid $global-border-color;

    .row {
      transition: all 0.5s ease;
    }

    .sidebar-column-icon {
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: $color-gray-light;
      transition: all 0.5s ease;
    }

    &:nth-of-type(even) {
      .sidebar-column-icon {
        background-color: $color-gray-lightest;
      }

      .path-primary {
        fill: $color-gray;
      }
    }

    &:active,
    &:focus,
    &:hover {
      .row {
        color: $white;
        background-color: $color-secondary-darkest;
      }

      .sidebar-column-icon {
        background-color: $color-secondary-darkest;
      }

      .path-primary {
        fill: $white;
      }
    }

    &:last-of-type {
      border-bottom: $global-border-width solid $global-border-color;
    }
  }

  h3 {
    font-size: 1.25em;
  }

  svg {
    max-width: 100%;
  }
}

.sidebar-single {
  time {
    font-family: $alt-font-family;
    font-size: 1em;
    font-weight: $global-alt-weight-bold;
  }
}
