::selection {
  color: $white;
  background: $color-secondary-darkest;
}

*,
*:before,
*:after {
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  box-sizing: border-box;
}

[ng\:cloak],
[ng-cloak],
[data-ng-cloak],
[x-ng-cloak],
.ng-cloak,
.x-ng-cloak {
  display: none;
}

a,
button,
input,
path,
polygon,
.button {
  transition: all 0.5s ease;

  path.nt {
    transition: fill 0.5s ease;
  }
}

button,
input,
optgroup,
select,
textarea {
  color: $color-base;
}

a {
  color: $color-primary;

  &:active,
  &:focus,
  &:hover {
    color: $color-primary;
  }
}

/**
 * Maximum Widths:
 *
 * `blockquote`, `p`, and `li` items have a maximum width of 40em for readability
 */

blockquote,
li,
p {
  max-width: 40em;
}

hr.ng-scope:last-child {
  display: none;
}

p,
li {
  line-height: 1.5;
}

.article-single {
  margin-left: 2em;

  h3 {
    color: $color-secondary-darkest;
  }
}

.input-group-button button {
  padding: 1em;

  @include breakpoint(medium) {
    padding: 0.85em 1em;
  }
}

.column-border {
  width: 4.5rem;

  &:first-child {
    border-right: $global-border-width solid $global-border-color;
  }
}

.columns.columns-content {
  @include breakpoint(medium) {
    padding: 2em;
  }
}

.fade.ng-enter {
  opacity: 0;
  transition: 0.5s linear all;

  &.ng-enter-active {
    opacity: 1;
  }
}

.main-content {
  margin-top: 54px;
  transition: margin-top 0.5s ease;
}

.max-global {
  position: relative;
  z-index: 1;
  max-width: $global-width;
}

.max-global-diff {
  font-size: 0.75em;

  @include breakpoint(medium) {
    max-width: 4.5em;
    font-size: 1em;
  }

  @include breakpoint(1512) {
    max-width: calc(100vw - 1440px);
  }
}

.navbar,
.sidebar {
  section {
    padding: 1em;
  }

  form {
    button,
    input,
    select,
    textarea {
      width: 100%;
    }
  }

  article {
    margin-bottom: 2em;
  }

  time {
    display: block;
    margin: 0 0 0.5em;
  }
}

.show-for-sr {
  width: 0;
  height: 0;
}

.show-on-focus {
  display: block;
  padding: 1em;
  font-weight: $global-weight-bold;
  color: $white;
  text-align: center;

  &:active,
  &:focus,
  &:hover {
    color: $white;
  }
}
