.icon-menu {
  position: relative;
  z-index: 2;
  background-color: $color-secondary-darkest;

  > nav {
    @include breakpoint(small only) {
      display: flex;
      max-width: 100%;
    }
  }

  button {
    padding: 0;
    font-family: $alt-font-family;
    font-size: 14px;
    font-weight: $global-alt-weight-bold;
    color: $white;
    text-align: left;
    text-transform: uppercase;

    @include breakpoint(small only) {
      flex: 1 1 auto;
    }

    @include breakpoint(medium) {
      display: flex;
      width: 100%;
      max-width: 480px;
      margin: $global-margin 0;
    }

    &:active,
    &:focus,
    &:hover {
      color: $color-primary-alt-light;

      a {
        color: $color-primary-alt-light;
      }

      svg {
        transform: scale(1.2);
        animation: hoverscale 1s 1 ease-in-out;
      }

      .path-primary {
        fill: $color-primary-alt-light;
      }
    }

    a {
      display: block;
      width: 100%;
      color: $white;
    }

    .row {
      width: 100%;

      > .columns {
        @include breakpoint(small only) {
          padding: 0.75em $global-padding 0.5em;
        }
      }
    }

    svg {
      max-width: 2em;

      @include breakpoint(medium) {
        max-width: 3em;
      }
    }
  }
}
