.header-bar.row {
  @include breakpoint(medium) {
    margin-bottom: 1rem;
  }

  h1 {
    padding: 0;

    @include breakpoint(large) {
      margin: 0;
    }
  }

  .site-tagline {
    max-width: 40em;
    padding: 0;
    margin: 0;
    font-size: 1em;

    @include breakpoint(large) {
      padding: 0 1rem;
      font-size: 0.85em;
    }
  }
}

.header-button {
  padding: 0;

  > button {
    position: relative;
    display: block;
    width: 1.5em;
    height: 1.5em;
    padding: 0.35em;
    font-size: 3em;
    line-height: 1;
    transform: rotate(0deg);
    transition: 0.5s ease;
  }
}

.header-button-menu {
  position: relative;
  z-index: 2;
  border-right: $global-border-width solid $color-gray;

  > div {
    display: flex;
    width: 4.5em;
    height: 4.5em;
    background-color: $color-gray;

    > button {
      width: 4.5em;
      height: 4.5em;
      font-size: 1em;
      background-color: $color-gray;
      transition: all 0s;

      &:active,
      &:focus,
      &:hover,
      &.open {
        span {
          background-color: $color-primary-alt-light;
        }
      }

      &[aria-expanded='true'] span {
        &:nth-child(1),
        &:nth-child(4) {
          top: 2em;
          left: 50%;
          width: 0%;
        }

        &:nth-child(2) {
          transform: rotate(45deg);
        }

        &:nth-child(3) {
          transform: rotate(-45deg);
        }
      }

      span {
        position: absolute;
        left: 1em;
        display: block;
        width: 2.5em;
        height: 0.5em;
        background: $white;
        border-radius: 0.5em;
        opacity: 1;
        transform: rotate(0deg);
        transition: 0.5s ease;

        &:nth-child(1) {
          top: 1em;
        }

        &:nth-child(2),
        &:nth-child(3) {
          top: 2em;
        }

        &:nth-child(4) {
          top: 3em;
        }
      }
    }
  }
}

.header-main {
  svg {
    max-width: 100%;
  }
}

.header-top {
  font-size: 0.75em;

  @include breakpoint(medium) {
    font-size: 1em;
  }
}

.official-website {
  font-family: $alt-font-family;
  font-size: 11px;
  font-weight: $global-alt-weight-normal;
  line-height: 20px;
  color: $white;
  text-transform: uppercase;
  background-color: $color-gray-dark;
  border-bottom: $global-border-width solid $color-gray;

  .tagline {
    padding: 0.25em 1.5em 0.25em 3.75em;
    background-image: url('../images/icons/flag.svg');
    background-position: 1.25em center;
    background-repeat: no-repeat;
    background-size: 21px;
  }
}

.row-header {
  color: $white;
  background-color: $color-gray-dark;

  &.sticky {
    position: fixed;
    width: 100%;
    max-height: 400px;
    transition: max-height 0.5s ease;
  }

  h1 {
    font-size: 2.5em;
    line-height: 1;

    a {
      border-bottom: 0;
    }
  }

  a {
    color: $white;

    &:active,
    &:focus,
    &:hover {
      color: $color-primary-alt-light;
    }
  }
}
